.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100vw;
  height: 10vh;
  background-color: rgb(76, 0, 197);
  color: rgb(248, 245, 245);
}
.navbar ul {
  list-style: none;
}
.navbar ul a {
  text-decoration: none;
  color: #c7c7c7;
  transition: 0.15s cubic-bezier(0.6, 0.04, 0.98, 0.335);
}
.navbar ul a:hover, .navbar ul a:focus {
  color: rgb(248, 245, 245);
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  width: 100vw;
  position: relative;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

.form__container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 70vw;
  height: fit-content;
  margin-top: 1rem;
  border-radius: 1rem;
  padding: 1rem;
  background-color: rgb(248, 245, 245);
}
.form__container * > input {
  transition: 0.15s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  transition: 0.3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  padding: 7px;
  margin-bottom: 0.2rem;
  font-size: 1rem;
  border: 2px solid rgb(65, 60, 60);
  border-radius: 0.3rem;
}
.form__container * > input:hover, .form__container * > input:focus {
  border-color: rgb(76, 0, 197);
}
.form__container * > input:hover, .form__container * > input:focus {
  background: rgb(233, 233, 233);
}
.form__container * > p {
  font-size: 20px;
}
.form__container .btn {
  transition: 0.15s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  transition: 0.3s cubic-bezier(0.6, 0.04, 0.98, 0.335);
  padding: 10px 15px;
  background: transparent;
  border: 1px solid #c7c7c7;
  cursor: pointer;
}
.form__container .btn:hover, .form__container .btn:focus {
  border-color: rgb(76, 0, 197);
}
.form__container .btn:hover, .form__container .btn:focus {
  background: rgb(233, 233, 233);
}
.form__container .label__con {
  padding-top: 0.5rem;
}
.form__container .submitBtn {
  margin-top: 1rem;
}

.output__con {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50vw;
  height: fit-content;
  margin-top: 1rem;
  border-radius: 1rem;
  padding: 1rem;
  background-color: rgb(248, 245, 245);
  display: none;
}
.output__con.active {
  display: flex;
}